@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap')

$componentWidth: 500px
$componentRadius: 15px
$chartHeight: 220px
$expenseComponentHeight: 400px

$mobileSize: 960px

$componentMobileWidth: 400px

body
    background-color: hsl(27, 66%, 92%)


main
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    height: 100vh
    font-family: 'DM Sans', sans-serif

.balance-component
    background-color: hsl(10, 79%, 65%)
    width: $componentWidth
    height: 100px
    border-radius: $componentRadius
    color: hsl(33, 100%, 98%)

.balance-info
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    height: 100%
    width: $componentWidth - 50px
    margin: auto

.balance-header
    font-size: 0.8em

.balance-value
    font-size: 1.5em

@media (max-width: $mobileSize)
    .balance-component
        width: $componentMobileWidth

    .balance-info
        width: $componentMobileWidth - 50px


.expense-component
    width: $componentWidth
    height: $expenseComponentHeight
    background-color: hsl(33, 100%, 98%)
    margin-top: 20px
    border-radius: $componentRadius

@media (max-width: $mobileSize)
    .expense-component
        width: $componentMobileWidth

h1
    font-size: 1.5em
    color: hsl(25, 47%, 15%)

.expense-info
    width: $componentWidth - 50px
    margin: auto

@media (max-width: $mobileSize)
    .expense-info
        width: $componentMobileWidth - 50px

.expense-chart
    display: flex
    flex-direction: row
    height: $chartHeight
    align-items: flex-end

.chart-amount
    font-size: 0.7em
    text-align: center
    color: hsl(27, 66%, 92%)
    background-color: hsl(25, 47%, 15%)
    width: 90%
    margin: 0 auto 5px
    border-radius: 5px
    display: none


.chart-column
    display: flex
    flex-direction: column
    justify-content: flex-end
    height: 100%
    position: relative
    width: ($componentWidth - 50px) / 7
    &:hover .chart-amount
        display: block


.chart-bar
    position: inherit
    height: 85%
    background-color: hsl(10, 79%, 65%)
    width: 35px
    border-radius: 5px
    margin: 0 auto
    cursor: pointer
    &:hover
        background-color: hsla(10, 79%, 65%, 0.5)

.chart-header
    text-align: center
    font-size: 0.8em

.chart-bar-bc-max
    background-color: hsl(186, 34%, 60%)
    &:hover
        background-color: hsla(186, 34%, 60%, 0.5)


hr
    border-top: 1px solid hsl(27, 66%, 92%)

.expense-footer
    margin-top: 20px

.expense-footer-info
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    height: 100%

.expense-footer-header
    font-size: 0.9em

.expense-footer-value
    font-size: 2em
    font-weight: 700

.expense-footer-month
    font-weight: 700
    font-size: 0.9em

.expense-footer-month-text    
    font-size: 0.9em

.expense-footer-right
    align-self: flex-end    

.expense-footer-left    
    align-self: flex-end        