@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap");
body {
  background-color: #f8e9dd;
}

main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  font-family: 'DM Sans', sans-serif;
}

.balance-component {
  background-color: #ec775f;
  width: 500px;
  height: 100px;
  border-radius: 15px;
  color: #fffaf5;
}

.balance-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  width: 450px;
  margin: auto;
}

.balance-header {
  font-size: 0.8em;
}

.balance-value {
  font-size: 1.5em;
}

@media (max-width: 960px) {
  .balance-component {
    width: 400px;
  }
  .balance-info {
    width: 350px;
  }
}

.expense-component {
  width: 500px;
  height: 400px;
  background-color: #fffaf5;
  margin-top: 20px;
  border-radius: 15px;
}

@media (max-width: 960px) {
  .expense-component {
    width: 400px;
  }
}

h1 {
  font-size: 1.5em;
  color: #382314;
}

.expense-info {
  width: 450px;
  margin: auto;
}

@media (max-width: 960px) {
  .expense-info {
    width: 350px;
  }
}

.expense-chart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  height: 220px;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

.chart-amount {
  font-size: 0.7em;
  text-align: center;
  color: #f8e9dd;
  background-color: #382314;
  width: 90%;
  margin: 0 auto 5px;
  border-radius: 5px;
  display: none;
}

.chart-column {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  height: 100%;
  position: relative;
  width: 64.28571px;
}

.chart-column:hover .chart-amount {
  display: block;
}

.chart-bar {
  position: inherit;
  height: 85%;
  background-color: #ec775f;
  width: 35px;
  border-radius: 5px;
  margin: 0 auto;
  cursor: pointer;
}

.chart-bar:hover {
  background-color: rgba(236, 119, 95, 0.5);
}

.chart-header {
  text-align: center;
  font-size: 0.8em;
}

.chart-bar-bc-max {
  background-color: #76b5bc;
}

.chart-bar-bc-max:hover {
  background-color: rgba(118, 181, 188, 0.5);
}

hr {
  border-top: 1px solid #f8e9dd;
}

.expense-footer {
  margin-top: 20px;
}

.expense-footer-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}

.expense-footer-header {
  font-size: 0.9em;
}

.expense-footer-value {
  font-size: 2em;
  font-weight: 700;
}

.expense-footer-month {
  font-weight: 700;
  font-size: 0.9em;
}

.expense-footer-month-text {
  font-size: 0.9em;
}

.expense-footer-right {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.expense-footer-left {
  -ms-flex-item-align: end;
      align-self: flex-end;
}
